export default {
	data() {
		return {
			id:0,
			detail:{}
		}
	},
	methods: {
		async getNewsDetail(){
			const res=await this.$myRequest({
				url:'/api/getnew/'+this.id
			})
			// console.log(res);
			this.detail=res.data.message[0];
		}
	},
	onLoad(options){
		this.id=options.id;
		this.getNewsDetail()
	}
}
